export default {
	data() {
		return {
			//所有参数
			allInfo: {},
			// banner
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 1000,
			//控制渐变标题栏的参数
			beforeHeaderzIndex: 11, //层级
			afterHeaderzIndex: 10, //层级
			beforeHeaderOpacity: 1, //不透明度
			afterHeaderOpacity: 0 //不透明度
		};
	},

	onPageScroll(e) {
		//导航栏渐变
		let tmpY = 375;
		e.scrollTop = e.scrollTop > tmpY ? 375 : e.scrollTop;
		this.afterHeaderOpacity = e.scrollTop * (1 / tmpY);
		this.beforeHeaderOpacity = 1 - this.afterHeaderOpacity;
		//切换层级
		this.beforeHeaderzIndex = e.scrollTop > 0 ? 10 : 11;
		this.afterHeaderzIndex = e.scrollTop > 0 ? 11 : 10;
	},
	onLoad() {
		this.getHomeAllInfo();
	},
	onPullDownRefresh() {
		this.getHomeAllInfo();
	},
	methods: {
		getHomeAllInfo() {
			let that = this;
			that.baseGet(
				that.U({ c: 'public_api', a: 'index' }),
				function(res) {
					that.allInfo = res.data;
					console.log(that.allInfo);
				},
				function(res) {
					console.log(res);
				},
				true
			);
			uni.stopPullDownRefresh();
		},
		search(e) {
			uni.navigateTo({
				url: '/pages/list/search/search'
			});
		},

		// 凤阳风光
		fengyangScenery(e) {
			uni.navigateTo({
				url: '/pages/list/fengyang_scenery/fengyang_scenery'
			});
		},
		// 凤阳集市
		fengyangFair(e) {
			uni.navigateTo({
				url: '/pages/tabber/fengyang_fair/fengyang_fair'
			});
		},
		// 农土特产
		agriculturalSpecialty(e) {
			uni.navigateTo({
				url: '/pages/list/agricultural_specialty/agricultural_specialty'
			});
		},
		// 理想生活
		idealLife(e) {
			uni.navigateTo({
				url: '/pages/list/ideal_life/ideal_life'
			});
		},
		// 商品详情
		goodeDetails(e) {
			uni.navigateTo({
				url: '/pages/list/goode_details/goode_details?id=' + e.currentTarget.dataset.id
			});
		},
		fengyang_scenery(e) {
			uni.navigateTo({
				url: '/pages/list/fengyang_scenery/fengyang_scenery'
			});
		},
		flowering_seasons(e) {
			uni.navigateTo({
				url: '/pages/list/flowering_seasons/flowering_seasons'
			});
		},
		fruit_garden(e) {
			uni.navigateTo({
				url: '/pages/list/fruit_garden/fruit_garden'
			});
		},
		line_details(e) {
			uni.navigateTo({
				url: '/pages/list/line_details/line_details?id='+e.currentTarget.dataset.id
			});
		}
	}
};
